@keyframes blink {
    0%{
        left: -130%;
    }
    100%{
        left: 130%;
    }

}

.default {

}

.red {
    background: red;
}

.button{
    position: relative;
    margin: 10px;
    overflow: hidden;
    cursor: pointer;
}
.button::after{
    display: block;
    content:'';
    position: absolute;

    left: -130%;
    top:0;
    height: 100%;
    width: 20%;

    transform: skew(45deg);

    background-color: #ffffff;
    opacity: 0.7;
    z-index: 14;
}

.button:hover::after{
    /*animation:  0.40s ease;*/
    animation: blink 0.35s ease;
}

.button:active{
    background-color: pink;
}
